body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
	font-size: 16px;
	margin: 0 auto;
	min-width: 300px;
	max-width: 1200px;
	line-height: 1.5;
}

.todo {
	background-color: rgb(143, 139, 139);
}

.checked {
	background-color:rgb(114, 216, 114);
}

.inProgress {
	background-color: rgb(235, 235, 134);
}

.toCheck {
	background-color:rgb(226, 117, 117);
}

.expired {
	background-color:rgb(122, 53, 53);
}

.mentee {
	padding: 5px;
	background-color: rgb(252, 185, 98);
	white-space: nowrap;
}

.mentee a {
	text-decoration: none;
	font-weight: 600;
	color:  rgb(101, 15, 240);
}

.marksTable {
	display: -webkit-flex;
	display: flex;
}

.taskName {	
	text-decoration: none;
	width: 25%;
	white-space: nowrap;
	line-height: 1.5;
	background-color: rgb(252, 185, 98);
}

.taskName a {
	text-decoration: none;
	color: rgb(101, 15, 240);
}

.taskMark {
	text-align: center;
}

td {
	border-radius: 3px;
}

.search__wrapper {
	display: -webkit-flex;
	display: flex;
	width: 100%;
	min-width: 300px;
	max-width: 600px;
	-webkit-align-items: center;
	        align-items: center;
	margin-left: 3px;
	margin-bottom: 10px;
}

.search {
	margin-left: 10px;
	min-width: 200px;
}

@media (max-width: 1220px) {
	.main {
	  
	  height: 100%;
	}

	.search__wrapper {
		max-width: 400px;
		
	}
  
	.marksTable {
		max-width: 400px;
	}
	
	.mentee {
		background-color: rgb(252, 185, 98);
		white-space: inherit;
		font-size: 14px;
	}

	.taskName {	
		text-decoration: none;
		width: 15%;
		white-space: inherit;
		line-height: 1.5;
		background-color: rgb(252, 185, 98);
		font-size: 14px;
	}

	tr td{
		white-space: inherit;
		max-width: 120px;
	}
  }
